.error404 {
    padding-bottom: 90px;
    &__block {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 40px;
        text-align: center;
        h1 {
            margin-bottom: 30px;
            font-size: 230px;
            span {
                margin-right: 0;
            }
            span:after {
                height: 39px;
                width: 39px;
                top: 57px;
                right: -34px;
            }
        }
        p {
            font-size: 33px;
            margin: 0 0 8px;
        }
        &>span {
            display: inline-block;
            width: 100%;
            padding: 0 15px;
            line-height: 23px;
        }
        a {
            margin-top: 40px;
        }
    }
}
@media(max-width: 767px) {
    .error404 {
        padding-bottom: 40px;
        &__block {
            margin-top: 25px;
            h1 {
                margin-bottom: 20px;
                font-size: 173px;
                span:after {
                    height: 29px;
                    width: 29px;
                    top: 43px;
                    right: -26px;
                }
            }
        }
        p {
            font-size: 25px;
        }
    }
}
@media(max-width: 575px) {
    .error404 {
        padding-bottom: 30px;
        &__block {
            margin-top: 15px;
            h1 {
                font-size: 117px;
                margin-bottom: 15px;
                span:after {
                    height: 18px;
                    width: 18px;
                    top: 30px;
                    right: -18px;
                }
            }
            a {
                margin-top: 30px;
            }
            p {
                font-size: 20px;
            }
            &>span {
                font-size: 14px;
                line-height: 21px;
            }
        }
    }
}